
$sizes : (
    "sm" : 400px,
    "nm" : 810px,
    "l" : 1024px,
    "xl" : 1280px,
    "xxl" : 1768px
);

@mixin sm-screen{
    @media (max-width: map-get($sizes, "sm")){
        @content;
    }
};

@mixin nm-screen{
    @media (max-width: map-get($sizes, "nm")){
        @content;
    }
};

@mixin l-screen{
    @media (max-width: map-get($sizes, "l")){
        @content;
    }
};

@mixin xl-screen{
    @media (max-width: map-get($sizes, "xl")){
        @content;
    }
};

@mixin xxl-screen{
    @media (max-width: map-get($sizes, "xxl")){
        @content;
    }
};

@mixin custom-screen($breakpoint: 1000px){
    @media (max-width: $breakpoint){
        @content
    }
}