@use "../../scss/variables" as *;
@use "../../scss/mixins" as *;

.wrapper{
    width: 100%;
    @include bebas;

    .searchContainer{
        width: 100%;
        display: flex;
        align-items: center;
        border-bottom: 1px solid $txt-main;

        .icon{
            font-size: $txt-nm-3;
        }

        .input{
            display: block;
            padding-block: $spacing-sm-1;
            padding-left: $spacing-sm-1;
            width: 100%;
            height: 100%;
            outline: none;
            border: none;
            font-size: $txt-nm-1;
            opacity: 0.9;
            background-color: transparent;
            @include bebas;
            
            &::placeholder{
                font-size: $txt-nm-1;
                @include bebas;
            }
        }
    }

    .descriptions{
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: $spacing-sm-4;
        font-size: $txt-nm-2;

        .sortContainer{
            position: relative; 
            z-index: 9;

            .sortLabel{
                display: flex;
                cursor: pointer;
            }

            .list{
                position: absolute;
                top: 110%;
                width: 150%;
                right: 0%;
                display: flex;
                flex-direction: column;
                gap: 10px;
                border: 1px solid $txt-main;
                padding: $spacing-sm-1;
                font-size: $txt-sm-4;
                background-color: $bg-main;

                .item{
                    cursor: pointer;

                    &:hover{
                        background-color: $gray;
                    }
                }
            }
        }
    }
}